.search-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-self: center;
	flex-direction: column;
}

.postcode-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 10px;
}

.search-wrapper > form {
	display: flex;
	justify-content: center;
	align-self: center;
	border: 1px solid;
}

form > input,
button {
	padding: 10px 20px;
	border-radius: 0;
	border: none;
	background-color: #fff;
	outline: none;
}

form > input {
	width: 250px;
}

form > button {
	border-left: 1px solid;
	font-weight: bold;
	width: 40px;
	background-size: auto 50%;
	background-repeat: no-repeat;
	background-position: center;
}

form > button:hover {
	background-color: #eee;
}

form > button:nth-child(2) {
	background-image: url(data:image/svg+xml;base64,);
}

form > button:nth-child(3) {
	background-image: url(data:image/svg+xml;base64,);
}
